import { Component, OnInit, AfterViewInit } from '@angular/core';
import * as SVG from 'svg.js';




@Component({
  selector: 'app-bike',
  templateUrl: './bike.component.html',
  styleUrls: ['./bike.component.css']
})
export class BikeComponent implements OnInit, AfterViewInit {
  static boardID = 'bike-road';
  static bikeID = 'bike';
  static frontID = 'front-wheel';
  static backID = 'back-wheel';

  bike_road_drawing: SVG.Doc;
  bike_drawing: SVG.Element;
  constructor() { }

  ngOnInit() {
  }
  ngAfterViewInit() {
    const _bike_board = document.getElementById(BikeComponent.boardID);
    this.bike_road_drawing = SVG(BikeComponent.boardID).size('100%', '100%');
    this.init();
  }

  frontWheel() {
    const _bike = document.getElementById(BikeComponent.bikeID).getBoundingClientRect();
    const _wheel = document.getElementById(BikeComponent.frontID).getBoundingClientRect();
    const bike = this.center(_bike);
    const wheel = this.center(_wheel);
    const x = ((wheel.x - _bike.left) / _bike.width) * 100 + '%';
    const y = ((wheel.y - _bike.top) / _bike.height) * 100 + '%';
    console.log(x, y);
    document.getElementById(BikeComponent.frontID).style.transformOrigin = x + ' ' + y;
  }

  backWheel() {
    const _bike = document.getElementById(BikeComponent.bikeID).getBoundingClientRect();
    const _wheel = document.getElementById(BikeComponent.backID).getBoundingClientRect();
    const bike = this.center(_bike);
    const wheel = this.center(_wheel);
    const x = ((wheel.x - _bike.left) / _bike.width) * 100 + '%';
    const y = ((wheel.y - _bike.top) / _bike.height) * 100 + '%';
    document.getElementById(BikeComponent.backID).style.transformOrigin = x + ' ' + y;
  }

  center(rect: ClientRect) {
    const x = (rect.left + rect.right) / 2;
    const y = (rect.top + rect.bottom) / 2;
    return { x: x, y: y };
  }

  drawRoad() {
    const size = this.getSize(BikeComponent.boardID);
    const road = this.bike_road_drawing.line(0, size.h, size.w, size.h).stroke({ width: 10 }).animate(2000);
  }

  getSize(id: string) {
    const _drawing = document.getElementById(id);
    const h = _drawing.clientHeight;
    const w = _drawing.clientWidth;
    return { h: h, w: w };
  }

  drawBoard() {
    this.bike_road_drawing.clear();
    this.drawRoad();
  }


  init() {
    this.drawBoard();
    this.frontWheel();
    this.backWheel();
  }

}





